.icontext2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }
  
  .icon-container {
    display: flex;
    font-size: 1.1rem;
    justify-content: flex-start;
    gap: 10px;
    background-color: #dcdcdc;
    padding: 1.6rem 4rem 1.6rem 1.8rem;
    border-radius: 2rem;
  }
  
  .primary-text {
    font-weight: 500;
  }
  
  .secondary-text {
    margin-top: 5px;
    margin-left: 3.75rem;
    color: #525C60;
    font-family: Open Sans;
    font-size: 1.125rem;
    font-weight: 400;
    width: 27.8rem;
  }
  